import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import CircularProgress from '@material-ui/core/CircularProgress';
// import * as yup from 'yup';
import { Form, Scope } from 'informed';
import { Mutation } from "react-apollo";

import Checkbox from '../formComponents/Checkbox';
import Text from '../formComponents/Text';
import Select from '../formComponents/Select';
import { FormHelperText } from '@material-ui/core';
// const validate = value => {
//   return !value || value.length < 5 ? 'Field must be at least five characters' : null;
// };

const styles = {
    root: {
        flexGrow: 1,
        marginTop: "5rem"
    },
    card: {
        minWidth: 275,
        margin: "0 2%"
    },
    header: {
        marginBottom: "1rem",
        fontSize: "2rem"
    },
    progress: {
        margin: "1rem",
    },
};

class FormGenerator extends Component {
    getJSX(obj) {
        console.log("getJSX...", obj);
        return Object.keys(obj).map(e => {
            let jsx = null;
            if (typeof (obj[e].default) === "string") {
                jsx = (<Text field={e} type={obj[e].type || "text"} initialValue={obj[e].default} startAdornment={obj[e].startAdornment} validateOnChange validateOnBlur></Text>);
            }
            else if (obj[e].default instanceof Array) {
                jsx = (<Select field={e} options={obj[e].default} startAdornment={obj[e].startAdornment} validateOnChange validateOnBlur></Select>);
            }
            else if (obj[e].default instanceof Date) {
                jsx = (<Text field={e} type={obj[e].type || "text"} initialValue={obj[e].default} startAdornment={obj[e].startAdornment} validateOnChange validateOnBlur></Text>);
            }
            else if (obj[e].type === Boolean) {
                jsx = (<Checkbox field={e} initialValue={obj[e].default} startAdornment={obj[e].startAdornment} validateOnChange validateOnBlur></Checkbox>);
            }
            else if (obj[e].default instanceof Object) {
                jsx = (<Scope scope={e}>
                    <Grid container justify="space-between" alignItems="center" align="center" className="mt4">
                        <Grid item xs={3}>
                            <InputLabel htmlFor={e} className="ttc mt5">{e}</InputLabel>
                        </Grid>
                        <Grid item xs={9}>
                            {this.getJSX(obj[e].default)}
                        </Grid>
                    </Grid>
                </Scope>);
            }
            else {
                throw new Error("undefined object found in form generator")
            }
            return (
                <React.Fragment>
                    {jsx}
                </React.Fragment>
            );
        });
    }
    render() {
        const {
            classes,
            history,
            title,
            submitText = "Submit",
            defaults,
            mutation,
            mutateFunc = (fn, data) => {
                return fn({ variables: { data } });
            },
            postMutation
        } = this.props;

        console.log(defaults);
        return (
            <Mutation mutation={mutation}>
                {(mutate, { loading, error, data }) => {
                    return (
                        <Form onSubmit={e => { mutateFunc(mutate, e).then((data) => { postMutation ? postMutation(data) : history.goBack() }) }}>
                            <Grid container className={classes.root} justify="center" alignItems="center" align="center">
                                <Grid item xs={12} sm={6} md={5}>
                                    <Card className={classes.card}>
                                        <CardContent className="ph4">
                                            <Typography variant="display1" className="mb2">{title}</Typography>
                                            {
                                                this.getJSX(defaults)
                                            }
                                        </CardContent>
                                        <CardActions>
                                            <Grid container justify="flex-end">
                                                {
                                                    loading ? <CircularProgress className={classes.progress} /> : null
                                                }
                                                {
                                                    error ? <FormHelperText error>{error.message}</FormHelperText> : null
                                                }
                                                <Button size="large" type="submit" disabled={loading}>{submitText}</Button>
                                            </Grid>
                                        </CardActions>
                                    </Card>
                                </Grid>
                            </Grid>
                        </Form>
                    );
                }}
            </Mutation>
        )
    }
};

export default withStyles(styles)(FormGenerator);



