import React, { Component } from 'react';
import FormGenerator from './FormGenerator';
import { Query } from 'react-apollo';
import { CREATESITE } from '../../graphql/mutation/remote';
import { USERS } from '../../graphql/query/remote';

class CreateSite extends Component {
    render() {
        return (
            <Query query={USERS}>
                {({ loading, error, data }) => {
                    if (loading) {
                        return (<span>loading...</span>);
                    }
                    else if (error) {
                        return (<span>Error Occured!</span>);
                    }
                    const defaults = {
                        name: {
                            type: "text",
                            default: "",
                        },
                        location: {
                            type: "text",
                            default: "",
                        },
                        manager: {
                            type: "select",
                            default: data.users
                        },
                        createdAt: {
                            type: "date",
                            default: new Date()
                        }
                    };
                    return (
                        <FormGenerator defaults={defaults} submitText="Create Site" mutation={CREATESITE} title="Create a new Site" {...this.props}></FormGenerator>
                    );
                }}
            </Query>
        )
    }
};

export default CreateSite;



